
@font-face {
    font-family: customfont;
    src: url(./font/AvenirLTStd-Medium.otf);
  }
body{
    background-color: #f2f2f2;
    font-family: customfont,sans-serif;
}
/*home page */
.btnhome{
  margin: 20px auto;
  width: 40%;
    background-color:gray;
    color: white;
    padding: 8px 20px;
    text-transform: uppercase;
    margin: 8px 0;
    border: none;
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
}
.container{
  padding-bottom: 20px;
}
.containerhome{
  margin: auto;
  max-width: 400px;
  padding: 40vh 0;
  background-color: white;
}
.row{
  text-align: center;
  max-width: 400px;
  max-height: 400px;
}

/*left arrow */

.fa-arrow-left{
  cursor: pointer;
  color: #686868;
}


.formbody{
    max-width: 400px !important;
    border-radius: 0px;
    margin: auto;
    margin-top: 20px;
    background-color: white;
    padding: 2px 20px 20px 20px;
}
.heading{
    color: #686868;
    margin-bottom: 40px;
    font-size: 25px;
}
.container h3 i{
    font-weight: 400;
}

input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 5px 0;
    display: inline-block;
    border: none;
    background-color: #f2f2f2;
    border-radius: 4px;
    box-sizing: border-box;
    font-weight: 600;
    font-size: 18px;
  }
  
  input[type=submit] {
    width: 100%;
    background-color:transparent;
    color: rgb(59, 59, 59);
    padding: 8px 20px;
    text-transform: uppercase;
    margin: 8px 0;
    border: 1px solid rgb(85, 85, 85);;
    border-radius: 4px;
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
  }
  
  input[type=submit]:hover {
    background-color: rgb(131, 128, 128);
  }
  .terms{
      color: #367588;
      text-align: center;
      padding: 0px 20px;
      font-size: 20px;
      margin-top: 10px;
  }
  .terms2{
      font-size: 14px;
      text-align: center;
      color: #8d8c8c;
  }

  .btn{
    width: 60%;
    background-color: #25D366;
    color: white;
    padding: 6px 0px;
    font-size: 12px;
    text-align: center;
    margin-left: 20%;
    border: none;
    font-weight: 600;
    border-radius: 15px;
    cursor: pointer;
  }

  /* notification setting */

  .togglehead{
      padding-left: 10px;
      font-size: 13px;
      color: #686868;
      max-width: 300px;
      margin-bottom: 40px;
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 25px;
  }
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    -webkit-transition: .4s;
    transition: .4s;
    border: 1px solid   black;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 21px;
    width: 25px;
    border: 1px solid black;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: white;
  }
  input:focus + .slider {
    box-shadow: 0 0 1px black;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(-24px);
    -ms-transform: translateX(-24px);
    transform: translateX(-24px);
  }

  /* Round */
.slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  /*subheading */

  .flex-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .flex-container > div {
    text-align: center;
    line-height: 0px;
  }
/*toggle items */
.toggleitems{
    font-size: 1.1rem;
    color: #686868;
}
.togglesubhead{
    color: #686868;
    margin-top: 15px;
    margin-bottom: 20px;
    font-size: 25px;
}
  